@import url(https://fonts.googleapis.com/css?family=Roboto:500);
*{
  color: white;
}
.App {
  font-family: 'Judson', serif;
  text-align: center;
  padding: 2% 5%;
  background-color: #1d1d1d ;
  
}

.App * {
  box-sizing: border-box;
}

ul {
  padding: 0;
  list-style: none;
}

p {
  margin: 5px 0;
}

.display-none {
  display: none;
}
.showSuggestions {
  display: block;
}

input[type="text"],
input[type="number"] {
  padding: 8px 6px 8px 10px;
  border-radius: 4px;
  outline: none;
  border: 1px solid #ccc;
  font-size: 14px;
  line-height: 20px;
  align-self: center;
  height: 38px;
  box-shadow: 0px 1px 10px 0 rgb(0 0 0 / 15%);
}


.city {
  width: 250px;
  margin-top: 10px;
}

.CitySearch {
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
  flex-basis: 300px;
  max-width: 300px;
  margin: auto;
  position: relative;
}

.suggestions {
  background-color: hsl(180, 20%, 99%);
  width: 250px;
  position: absolute;
  z-index: 2;
  align-self: center;
  margin: 0;
  top: 110px;
  font-size: 14px;
  box-shadow: 10px 10px 16px -12px rgba(0, 0, 0, 0.75);
  
}

.suggestions > li {
  text-align: left;
  padding: 8px 6px 8px 10px;
  background-color: #eee;
  cursor: default;
  color: black;
  font-weight: 600;
}

.suggestions > li:last-child {
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
}

b{
  color:black;
}

.suggestions > li:hover {
  background-color: #bcdcdb;
}

.info-alert {
  display: flex;
  justify-content: center;
  align-items: center;
  font-style: italic;
  height: 60px;
 
}

.error-alert {
  display: flex;
  justify-content: center;
  align-items: center;
  font-style: italic;
  height: 50px;
}

.event {
  background-color: hsl(180, 20%, 99%);
  padding: 10px 10px 35px;
  margin-bottom: 20px;
  text-align: left;
  box-shadow: 0px 1px 5px 5px rgb(0 0 0 / 15%);
  border-radius: 4px;
  position: relative;
}

.eventTitle{
  font-family: 'Archivo Black', sans-serif;
}

.details-btn {
  position: absolute;
  right: 5px;
  bottom: 5px;
  padding: 5px 8px;
  border-radius: 4px;
  outline: none;
  cursor: pointer;
}

.name {
  font-size: 18px;
  font-weight: 600;
}

.group-name {
  text-transform: uppercase;
  font-size: 14px;
}

.going,
.visibility {
  font-style: italic;
}

.description {
  overflow-x: hidden;
}

.numberOfEvents {
  width: 200px;
  margin: 20px auto 40px auto;
  text-align: center;
  color: black;
}

.button-wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 20px;
  padding: 20px;
  font-family: sans-serif;
}



.data-vis-wrapper {
  display: flex;
  align-items: center;
  flex-direction: column;
}
.data-vis-wrapper .recharts-responsive-container {
  min-width: 100%;
  display: flex;
}
.recharts-surface {
  overflow: visible;
}

.login-button {
  color: #494949;
  text-transform: uppercase;
  text-decoration: none;
  background: #ffffff;
  padding: 20px;
  border: 4px solid #494949;
  display: inline-block;
  transition: all 0.4s ease 0s;
  min-width: 10rem;
  margin: 1rem;
}

.google-btn {
  width: 184px;
  height: 42px;
  margin: 1rem;
  background-color: #4285f4;
  border-radius: 2px;
  box-shadow: 0 3px 4px 0 rgba(0, 0, 0, 0.25);
}
.google-btn:hover {
  box-shadow: 0 0 6px #4285f4;
}
.google-btn:active {
  background: #1669f2;
}
.google-icon-wrapper {
  position: absolute;
  margin-top: 1px;
  margin-left: 1px;
  width: 40px;
  height: 40px;
  border-radius: 2px;
  background-color: #fff;
}
.google-icon {
  position: absolute;
  margin-top: 11px;
  margin-left: -8px;
  width: 18px;
  height: 18px;
}
.collapseDetailsBtn,
.expandDetailsBtn {
  float: right;
  margin: 11px 11px 0 0;
  color: rgb(2, 0, 0);
  background-color: transparent;
  font-size: 14px;
  letter-spacing: 0.2px;
  border: 1px solid rgb(179, 171, 171);
  cursor: pointer;
}


@media only screen and (max-width: 768px) {
  
  .error-alert {
    margin: 20px auto;
  }
}

@media only screen and (min-width: 992px) {
  .data-vis-wrapper {
    flex-direction: row;
  }
  .data-vis-wrapper .recharts-responsive-container {
    flex-basis: 50%;
    max-width: 50%;
    min-width: auto;
    margin: 0 auto;
  }
}